'use client'
import { useState, useEffect } from 'react'
import { 
    Star, 
    Calendar, 
    Clock, 
    CreditCard, 
    Check, 
    X,
    Zap,
    Users,
    MessageCircle,
    FileText,
    Code,
    Users2
} from 'lucide-react'

export default function SubscriptionPage() {
    const [isPro, setIsPro] = useState(false)
    const [showUpgradeModal, setShowUpgradeModal] = useState(false)
    const [showCancelModal, setShowCancelModal] = useState(false)
    const [showUpdatePaymentModal, setShowUpdatePaymentModal] = useState(false)
    const [loading, setLoading] = useState(false)

    // Mock user data - replace with your actual data fetching
    const user = {
        name: 'Admin User',
        email: 'admin@qwesi.com',
        createdAt: '2024-01-15T10:30:00Z',
        subscription: {
            currentPeriodEnd: '2025-01-15T10:30:00Z'
        },
        paymentMethod: {
            last4: '4242'
        }
    }

    const formatDate = (dateString) => {
        return new Date(dateString).toLocaleDateString(undefined, {
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        })
    }

    const handleUpgrade = async () => {
        setLoading(true)
        try {
            const response = await fetch('https://dark-caldron-448714-u5.appspot.com/api/subscription/checkout', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}` // Adjust based on your auth implementation
                },
                body: JSON.stringify({
                    planType: 'pro',
                    returnUrl: window.location.origin + '/subscription?success=true',
                    cancelUrl: window.location.origin + '/subscription?cancelled=true'
                })
            })

            const data = await response.json()
            
            if (response.ok && data.checkoutUrl) {
                // Redirect to Stripe checkout
                window.location.href = data.checkoutUrl
            } else {
                throw new Error(data.message || 'Failed to create checkout session')
            }
        } catch (error) {
            console.error('Checkout error:', error)
            alert('Failed to start checkout process. Please try again.')
            setLoading(false)
        }
    }

    const handleCancel = async () => {
        setLoading(true)
        try {
            const response = await fetch('https://dark-caldron-448714-u5.appspot.com/api/subscription/cancel', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}` // Adjust based on your auth implementation
                }
            })

            const data = await response.json()
            
            if (response.ok) {
                setIsPro(false)
                setShowCancelModal(false)
                alert('Subscription cancelled successfully. You will retain access until the end of your current billing period.')
            } else {
                throw new Error(data.message || 'Failed to cancel subscription')
            }
        } catch (error) {
            console.error('Cancel error:', error)
            alert('Failed to cancel subscription. Please try again or contact support.')
        } finally {
            setLoading(false)
        }
    }

    return (
        <div className="min-h-screen bg-slate-50">
            {/* Modern Header */}
            <header className="relative border-b border-slate-200 backdrop-blur-xl">
                <div className="absolute inset-0 bg-white"></div>
                <div className="relative z-10 px-6 py-6">
                    <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-3 sm:space-y-0">
                        <div className="text-center sm:text-left">
                            <h1 className="text-2xl font-black text-slate-900 tracking-tight">
                                SUBSCRIPTION MANAGEMENT
                            </h1>
                            <p className="text-sm font-medium text-slate-600 mt-1">
                                Manage your <span className="font-bold text-blue-600">qwesi AI subscription</span>
                            </p>
                        </div>
                        
                        {/* Decorative orbs */}
                        <div className="hidden sm:block relative">
                            <div className="absolute -inset-4 pointer-events-none">
                                <div className="absolute w-2 h-2 bg-blue-400 rounded-full opacity-20 top-2 right-8"></div>
                                <div className="absolute w-1.5 h-1.5 bg-orange-400 rounded-full opacity-20 top-6 right-4"></div>
                                <div className="absolute w-1 h-1 bg-blue-500 rounded-full opacity-30 top-4 right-12"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            <main className="flex-1 p-6 space-y-8">
                <div className="max-w-7xl mx-auto w-full">
                    {/* Current Plan Status Hero Card */}
                    <div className="relative rounded-3xl overflow-hidden mb-8 backdrop-blur-xl">
                        <div className="absolute inset-0 bg-blue-600"></div>
                        
                        <div className="relative z-10 p-8 text-white">
                            <div className="space-y-8">
                                {/* Plan Info */}
                                <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-4 sm:space-y-0">
                                    <div className="flex items-center space-x-4">
                                        <div className="w-16 h-16 bg-white/20 rounded-2xl flex items-center justify-center backdrop-blur-sm shadow-lg">
                                            <Star className="w-8 h-8" />
                                        </div>
                                        <div className="space-y-1">
                                            <h2 className="text-2xl font-black text-white">
                                                {isPro ? 'qwesi AI Pro' : 'qwesi AI Free'}
                                            </h2>
                                            <p className="text-blue-100">
                                                {isPro 
                                                    ? 'You have access to all Pro features and unlimited AI queries.'
                                                    : 'Upgrade to Pro to unlock advanced AI models and unlimited usage.'
                                                }
                                            </p>
                                        </div>
                                    </div>
                                    <div className="text-center sm:text-right">
                                        <div className="text-4xl font-black text-white">
                                            {isPro ? '$4.99' : '$0'}
                                        </div>
                                        <div className="text-blue-200">per month</div>
                                    </div>
                                </div>

                                {/* Subscription Details */}
                                <div className="border-t border-white/20 pt-6">
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                        <div className="flex items-center space-x-3">
                                            <Calendar className="w-5 h-5 text-blue-200 flex-shrink-0" />
                                            <div className="space-y-1">
                                                <div className="text-sm text-blue-200">
                                                    {isPro ? 'Started' : 'Account Created'}
                                                </div>
                                                <div className="font-bold text-white">
                                                    {formatDate(user.createdAt)}
                                                </div>
                                            </div>
                                        </div>
                                        <div className="flex items-center space-x-3">
                                            <Clock className="w-5 h-5 text-blue-200 flex-shrink-0" />
                                            <div className="space-y-1">
                                                <div className="text-sm text-blue-200">
                                                    {isPro ? 'Next Billing' : 'Upgrade Available'}
                                                </div>
                                                <div className="font-bold text-white">
                                                    {isPro ? formatDate(user.subscription.currentPeriodEnd) : 'Anytime'}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                {/* Payment Method */}
                                {isPro && user.paymentMethod && (
                                    <div className="border-t border-white/20 pt-6">
                                        <div className="flex items-center justify-between">
                                            <div className="flex items-center space-x-3">
                                                <CreditCard className="w-5 h-5 text-blue-200 flex-shrink-0" />
                                                <div className="space-y-1">
                                                    <div className="text-sm text-blue-200">Payment Method</div>
                                                    <div className="font-bold text-white">
                                                        VISA •••• {user.paymentMethod.last4}
                                                    </div>
                                                </div>
                                            </div>
                                            <button 
                                                onClick={() => setShowUpdatePaymentModal(true)}
                                                className="px-4 py-2 bg-white/20 text-white rounded-2xl hover:bg-white/30 transition-all duration-300 hover:scale-105 text-sm font-bold backdrop-blur-sm"
                                            >
                                                Update
                                            </button>
                                        </div>
                                    </div>
                                )}
                            </div>

                            {/* Decorations */}
                            <div className="absolute inset-0 pointer-events-none overflow-hidden">
                                <div className="absolute w-32 h-32 bg-white/10 rounded-full top-8 right-16"></div>
                                <div className="absolute w-20 h-20 bg-orange-300/10 rounded-full bottom-12 left-20"></div>
                                <div className="absolute w-24 h-24 bg-blue-300/10 rounded-full top-20 right-1/3"></div>
                            </div>
                        </div>
                    </div>

                    {/* Plans Section */}
                    <div className="space-y-8">
                        <div className="flex items-center space-x-4">
                            <h3 className="text-2xl font-bold text-slate-900">Choose Your Plan</h3>
                            <div className="flex-1 h-px bg-slate-300"></div>
                        </div>

                        <div className="grid md:grid-cols-2 gap-8">
                            {/* Free Plan Card */}
                            <div className={`relative rounded-3xl overflow-hidden transition-all duration-300 backdrop-blur-xl ${!isPro ? 'ring-2 ring-blue-500/50 shadow-2xl shadow-blue-500/10' : ''}`}>
                                <div className="absolute inset-0 bg-white/70 border border-slate-200/60"></div>
                                
                                {!isPro && (
                                    <div className="absolute -top-4 left-1/2 transform -translate-x-1/2 z-20 bg-blue-500 text-white px-6 py-2 rounded-full text-sm font-bold flex items-center space-x-2 shadow-lg">
                                        <Check className="w-4 h-4" />
                                        <span>Current Plan</span>
                                    </div>
                                )}

                                <div className="relative z-10 p-6 mt-4">
                                    <div className="border-b border-slate-200/40 pb-6 mb-6">
                                        <div className="text-center mb-6">
                                            <h3 className="text-xl font-bold text-slate-900 mb-2">Free Plan</h3>
                                            <p className="text-slate-600">Perfect for getting started</p>
                                        </div>
                                        <div className="text-center">
                                            <div className="text-4xl font-black text-slate-900">$0</div>
                                            <div className="text-slate-500">forever</div>
                                        </div>
                                    </div>

                                    <div className="space-y-6">
                                        <ul className="space-y-4">
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Check className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Basic AI Assistant</div>
                                                    <div className="text-sm text-slate-600">Access to core AI features</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Check className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">10 Queries per Day</div>
                                                    <div className="text-sm text-slate-600">Perfect for light usage</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Check className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Email Support</div>
                                                    <div className="text-sm text-slate-600">48-hour response time</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Check className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Basic Templates</div>
                                                    <div className="text-sm text-slate-600">Access to 5 templates</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3 opacity-50">
                                                <div className="w-5 h-5 rounded-full bg-slate-100 text-slate-400 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <X className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-500">No API Access</div>
                                                </div>
                                            </li>
                                        </ul>

                                        <button 
                                            className={`w-full px-6 py-3 rounded-2xl font-bold transition-colors duration-300 focus:outline-none focus:ring-4 ${
                                                !isPro 
                                                    ? 'bg-slate-100 text-slate-600 cursor-not-allowed' 
                                                    : 'bg-blue-500 text-white hover:bg-blue-600 focus:ring-blue-500/20'
                                            }`}
                                            disabled={!isPro}
                                            onClick={() => isPro && setShowUpgradeModal(true)}
                                        >
                                            {!isPro ? 'Current Plan' : 'Downgrade to Free'}
                                        </button>
                                    </div>
                                </div>
                            </div>

                            {/* Pro Plan Card */}
                            <div className={`relative rounded-3xl overflow-hidden transition-all duration-300 backdrop-blur-xl ${isPro ? 'ring-2 ring-orange-500/50 shadow-2xl shadow-orange-500/10' : ''}`}>
                                <div className="absolute inset-0 bg-white/70 border border-slate-200/60"></div>
                                
                                {isPro && (
                                    <div className="absolute -top-4 left-1/2 transform -translate-x-1/2 z-20 bg-orange-500 text-white px-6 py-2 rounded-full text-sm font-bold flex items-center space-x-2 shadow-lg">
                                        <Check className="w-4 h-4" />
                                        <span>Current Plan</span>
                                    </div>
                                )}

                                <div className="relative z-10 p-6 mt-4">
                                    <div className="border-b border-slate-200/40 pb-6 mb-6">
                                        <div className="text-center mb-6">
                                            <h3 className="text-xl font-bold text-slate-900 mb-2">Pro Plan</h3>
                                            <p className="text-slate-600">For power users and professionals</p>
                                        </div>
                                        <div className="text-center">
                                            <div className="text-4xl font-black text-slate-900">$4.99</div>
                                            <div className="text-slate-500">per month</div>
                                        </div>
                                    </div>

                                    <div className="space-y-6">
                                        <ul className="space-y-4">
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Zap className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Advanced AI Models</div>
                                                    <div className="text-sm text-slate-600">GPT-4, Claude, and more</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Check className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Unlimited Queries</div>
                                                    <div className="text-sm text-slate-600">No daily limits</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <MessageCircle className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Priority Support</div>
                                                    <div className="text-sm text-slate-600">24/7 chat support</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <FileText className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">100+ Premium Templates</div>
                                                    <div className="text-sm text-slate-600">Industry-specific templates</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Code className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">API Access</div>
                                                    <div className="text-sm text-slate-600">Integrate with your apps</div>
                                                </div>
                                            </li>
                                            <li className="flex items-start space-x-3">
                                                <div className="w-5 h-5 rounded-full bg-orange-100 text-orange-600 flex items-center justify-center flex-shrink-0 mt-0.5">
                                                    <Users2 className="w-3 h-3" />
                                                </div>
                                                <div className="flex-1">
                                                    <div className="font-bold text-slate-900">Team Collaboration</div>
                                                    <div className="text-sm text-slate-600">Share projects with team</div>
                                                </div>
                                            </li>
                                        </ul>

                                        <button 
                                            onClick={() => isPro ? setShowCancelModal(true) : setShowUpgradeModal(true)}
                                            className={`w-full px-6 py-3 rounded-2xl font-bold transition-colors duration-300 focus:outline-none focus:ring-4 ${
                                                isPro 
                                                    ? 'bg-red-500 text-white hover:bg-red-600 focus:ring-red-500/20' 
                                                    : 'bg-orange-500 text-white hover:bg-orange-600 focus:ring-orange-500/20'
                                            }`}
                                        >
                                            {isPro ? 'Cancel Subscription' : 'Upgrade to Pro'}
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

            {/* Modals */}
            {showUpgradeModal && (
                <Modal 
                    title="Upgrade to Pro"
                    onClose={() => setShowUpgradeModal(false)}
                    loading={loading}
                >
                    <div className="text-center space-y-4">
                        <p className="text-slate-600">Ready to unlock the full power of qwesi AI?</p>
                        <div className="flex gap-3">
                            <button 
                                onClick={() => setShowUpgradeModal(false)}
                                className="flex-1 px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg transition-colors"
                                disabled={loading}
                            >
                                Cancel
                            </button>
                            <button 
                                onClick={handleUpgrade}
                                disabled={loading}
                                className="flex-1 px-4 py-2 bg-orange-600 hover:bg-orange-700 text-white rounded-lg transition-colors disabled:opacity-50"
                            >
                                {loading ? 'Processing...' : 'Upgrade Now'}
                            </button>
                        </div>
                    </div>
                </Modal>
            )}

            {showCancelModal && (
                <Modal 
                    title="Cancel Subscription"
                    onClose={() => setShowCancelModal(false)}
                    loading={loading}
                >
                    <div className="text-center space-y-4">
                        <p className="text-slate-600">Are you sure you want to cancel your Pro subscription?</p>
                        <div className="flex gap-3">
                            <button 
                                onClick={() => setShowCancelModal(false)}
                                className="flex-1 px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg transition-colors"
                                disabled={loading}
                            >
                                Keep Pro
                            </button>
                            <button 
                                onClick={handleCancel}
                                disabled={loading}
                                className="flex-1 px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded-lg transition-colors disabled:opacity-50"
                            >
                                {loading ? 'Processing...' : 'Cancel Subscription'}
                            </button>
                        </div>
                    </div>
                </Modal>
            )}

            {/* Update Payment Modal */}
            {showUpdatePaymentModal && (
                <Modal 
                    title="Update Payment Method"
                    onClose={() => setShowUpdatePaymentModal(false)}
                    loading={loading}
                >
                    <div className="space-y-4">
                        <input 
                            type="text" 
                            placeholder="Card Number"
                            className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                        />
                        <div className="grid grid-cols-2 gap-3">
                            <input 
                                type="text" 
                                placeholder="MM/YY"
                                className="px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                            />
                            <input 
                                type="text" 
                                placeholder="CVC"
                                className="px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                            />
                        </div>
                        <div className="flex gap-3">
                            <button 
                                onClick={() => setShowUpdatePaymentModal(false)}
                                className="flex-1 px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg transition-colors"
                                disabled={loading}
                            >
                                Cancel
                            </button>
                            <button 
                                onClick={() => handleUpdatePayment({ paymentMethodId: 'pm_' + Date.now() })}
                                disabled={loading}
                                className="flex-1 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors disabled:opacity-50"
                            >
                                {loading ? 'Updating...' : 'Update Payment'}
                            </button>
                        </div>
                    </div>
                </Modal>
            )}
        </div>
    )
}

// Modal Component
const Modal = ({ title, children, onClose, loading }) => (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
        <div className="bg-white rounded-lg max-w-md w-full">
            <div className="px-6 py-4 border-b border-slate-200">
                <div className="flex items-center justify-between">
                    <h2 className="text-xl font-bold text-slate-800">{title}</h2>
                    <button 
                        onClick={onClose} 
                        className="p-2 hover:bg-slate-100 rounded-lg"
                        disabled={loading}
                    >
                        <X size={20} className="text-slate-600" />
                    </button>
                </div>
            </div>
            <div className="p-6">
                {children}
            </div>
        </div>
    </div>
)